@import "../../common/colors.scss";

.movie-list {
    margin: 50px 0px;

    h2 {
        color:$font-secondary;
        margin-bottom: 30px;
        font-weight: 400;
        text-align: center;
    }
}

.movie-container {
    display: grid;
    justify-content: center;
    margin: 5px;
    grid-template-columns: repeat(auto-fill, minmax(220px, 0fr));
    gap: 15px;
}