@import "../../common/colors.scss";
@import "../../common/mediaqueries.scss";

.header {
    background-color: $secondary-color;
    height: 72px;
    padding: 0px 40px;
    display: flex;
    align-items: center;
}

.logo {
    color: $font-primary;
    font-size: 20px;
    font-weight: 300;
    text-transform: uppercase;
}

.user-image, .user-image img {
    z-index: 1;
    position: fixed;
    top: -10px;
    width: 100px;
    right: 5px;
    transition: 0.2s ease-in-out;

    :not(:hover) {
        opacity: 0.3;
        scale: 1;
    }

    :hover {
        opacity: 1; 
        scale: 1.25;
    }

}

.user-image img {
    border-style:solid;
    border-radius: 100%;
    border-color: white;
}