@import "../../common/colors.scss";
@import "../../common/mediaqueries.scss";


@media (max-width: $mobile) {
    .movie-title {
        text-align: center;
        padding-bottom: 15px;
    }

    .movie-img {
        display: flex;
        justify-content: center;
    }
    
}

.movie-section {
    display: flex;
    justify-content: space-evenly;
    padding: 40px 0px;
    color: $font-primary;
    font-weight: 400;
}

.movie-rating {
    padding-left: 3px;
    margin-top: 20px;
    color: $font-secondary;
    display: flex;
    span {
        margin-right: 20px;
    }
}

.movie-title {
    font-size: 40px;
    color: $font-primary;
}

.movie-plot {
    margin-top: 1pc;
    line-height: 1.8rem;
}

.movie-info {
    padding-top: 1pc;
}

.movie-info > div span:first-child {
    padding: 0.2pc 0px;
    color: $font-primary;
    font-weight: 600;
    width: 100px;
    display: inline-block;
}

.movie-info > div span {
    color: $font-secondary;
}

.section-right {
    margin-right: 15px;
}

.section-left {
    margin-left: 15px;
}