@import "../../common/colors.scss";

.card-item {
    background: $secondary-color;
    cursor: pointer;
    box-shadow: 5px 5px rgb(39, 3, 3);
}

.card-top {
    height: 300px;

    img {
        width: 100%;
        height: 100%;
        transition: 0.2s ease;
    }

        :hover {
            scale: (1.5);
            box-shadow: 5px 5px rgb(0, 0, 0);
        }

}

.card-info {
    color: $font-primary;
    text-align: center;

    h4 {
        font-weight: 400;
        margin-bottom: 10px;
    }
}

.card-bottom {
    padding: 5px;
}