@import "../../common/colors.scss";
@import "../../common/mediaqueries.scss";

.search-bar-container {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}

.search-bar > input {
    width: 50vw;
    height: 3em;
}

.search-bar > button {
    border-style: none;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    width: 6vw;
    height: 2.9em;
    transition: 0.2s ease-in-out;
}